export function* watchGetLabels() {
  yield takeEvery(GET_LABELS, getStatsLabels);
}

export function* watchGetProperties() {
  yield takeEvery(GET_PROPERTIES, getStatsProperties);
}

export function* watchGetObjects() {
  yield takeEvery(GET_OBJECTS, getStatsObjects);
}

export function* watchGetTexts() {
  yield takeEvery(GET_TEXTS, getStatsTexts);
}

export function* watchGetPhtoScore() {
  yield takeEvery(GET_PHOTO_SCORE, getStatsPhtoScore);
}

export function* watchGetFaces() {
  yield takeEvery(GET_FACES, getFaces);
}

export default function* rootSaga() {
  yield all([
    fork(watchGetLabels),
    fork(watchGetProperties),
    fork(watchGetObjects),
    fork(watchGetTexts),
    fork(watchGetPhtoScore),
    fork(watchGetFaces),
  ]);
}
